.logo[href] {
  &:hover,
  &:focus {
    opacity: 0.8;
  }
  
  :active {
    opacity: 0.6;
  }
}

.logo__img {
  width: 190px;
  height: 38px;
  
  @media (min-width: @tablet-width) {
    width: 174px;
    height: 59px;
  }

  @media (min-width: @desktop-width) {
    width: 203px;
    height: 60px;
  }
}